import React from 'react';
import './UpcomingMovies.css';
import omdbApiService from '../../services/OmdbApiService.js';
import { MoviePreview } from '../MoviePreview/MoviePreview.js';

export class UpcomingMovies extends React.Component {
    constructor(props) {
        super(props);
        this.state = { movies: [] };
    }

    componentDidMount() {
        let moviePromises = 'qwertyuiopasdfghjkl'.split('').map(m => omdbApiService.searchByName(m, new Date().getFullYear(), 'movie'));
        Promise.all(moviePromises)
            .then(responses => {
                const result = [];
                const map = new Map();
                const filteredResponses = responses
                    .filter(r => r.data.Response !== 'False' && r.data.Poster !== 'N/A').map(r => r.data);
                for (let data of filteredResponses) {
                    if (!map.has(data.imdbID)) {
                        map.set(data.imdbID, true);
                        result.push(data);
                    }
                }
                const sortFunction = (a, b) => {
                    const aReleased = new Date(a.Released === 'N/A' ? 8640000000000000 : a.Released);
                    const bReleased = new Date(b.Released === 'N/A' ? 8640000000000000 : b.Released);
                    return aReleased < bReleased ? -1 : 1;
                };
                this.setState({
                    movies: result.sort(sortFunction)});
            });

    }

    render() {
        let header = this.state.movies.length > 0 ? `New movies in ${new Date().getFullYear()}` : '';
        return (
            <>
                <h1>{header}</h1>
                {this.state.movies.map(m => (<MoviePreview movie={m} key={m.imdbID} />))}
            </>
        );
    }
}