import React from 'react';
import omdbApiService from '../../services/OmdbApiService.js';

export class MovieDetail extends React.Component {
    movieProperties = ['Year', 'Rated', 'Released', 'Runtime', 'Genre', 'Director', 'Writer', 'Actors', 'Plot', 'Language', 'Country', 'Awards', 'DVD', 'BoxOffice', 'Production', 'Website'];
    seriesProperties = ['Year', 'Rated', 'Released', 'Runtime', 'Genre', 'Director', 'Writer', 'Actors', 'Plot', 'Language', 'Country', 'Awards', 'totalSeasons'];

    constructor(props) {
        super(props);
        this.state = {
            movie: null
        };
        this.componentDidMount = this.componentDidMount.bind(this);
    }

    componentDidMount() {
        omdbApiService.searchById(this.props.match.params.id)
            .then(resp => {
                this.setState({ movie: resp.data });
            });
    }

    render() {
        const m = this.state.movie;
        if (!m)
            return (null);
        let detail = <></>;
        const properties = m.Type === 'movie' ? this.movieProperties : (m.Type === 'series' ? this.seriesProperties : null);
        if (properties) {
            detail = Object.keys(m).map(k => properties.indexOf(k) > -1 ? (<div><strong>{k}</strong>: {m[k]}</div>) : <></>);
        }
        return (
            <div className="row">
                <h1 className="col-12">{this.state.movie.Title}</h1>
                <div className="col-2">
                    <img alt={this.state.movie.Title} className="movie-poster" src={this.state.movie.Poster} />
                </div>
                <div className="col-10">
                    <div className="float-left">
                        {detail}
                    </div>
                </div>
            </div>
        );
    }
}